// Detecting the environment
/* tslint:disable */
declare var window: {
    fin?: any;
    process?: any;
}
/* tslint:enable */

let isBrowser = false;

// Openfin check
const isFin = (typeof window !== 'undefined') && window.fin && window.fin.desktop; // jshint ignore:line

// Node js check
let isNode = false;
try { isNode = Object.prototype.toString.call(global.process) === '[object process]'; } catch (e) {}

// Electron check
/* tslint:disable */
let isElectron = (typeof window !== 'undefined') && window.process && window.process.versions && window.process.versions.electron; // jshint ignore:line
/* tslint:enable */

// Worker environment
/* tslint:disable */
let isWebWorker = !isNode && (typeof window === 'undefined') && (typeof module !== 'undefined') && (typeof module.exports !== 'undefined'); // jshint ignore:line
/* tslint:enable */

// else Browser environment
isBrowser = !isFin && !isNode && !isElectron;


/**
 * Browser environment detected.
 */
export const IS_BROWSER = isBrowser;

/**
 * Node.js (and derivatives) environment detected.
 */
export const IS_NODE = isNode;

/**
 * OpenFin environment detected.
 */
export const IS_OPENFIN = isFin;

/**
 * Electron environment detected.
 */
export const IS_ELECTRON = isElectron;

/**
 * WebWorker context detected.
 */
export const IS_WEBWORKER = isWebWorker;




